postDatas(credentials, type) {
  return new Promise((resolve, reject) => {
    let headers = new Headers();

    this.http.post(this.api+'apilogin/logarapp', JSON.stringify(credentials), {headers: headers})
      .subscribe(res => {
        resolve(res.json());
      }, (err) => {
        reject(err);
      });
  });

}

